.homework3 {
	display: flex;
	align-items: center;
	flex-direction: column;
	margin: 0 auto;
	padding: 10px 0px 0px 10px;
	background: #eaedf2;
	width: 245px;
	font-size: 14px;
	.listOfAddedUsers{
		align-self: flex-start;
	}
	.block {
		display: flex;
		flex-direction: column;
		.block_container {
			display: flex;
			flex-direction: column;
			align-items: center;
			.number_users{
				align-self: flex-start;
				margin: 0px 0px 5px 0px;
				.red{
					color: red;
					font-weight: 500;
				}
			}
			.error {
				box-shadow: inset 3px 4px 7px #d3acac, inset -3px -4px 3px white;
				background: #eaedf2;
				border: none;
				padding: 4px 10px;
				font-size: 14px;
				color: #5e5a57;
				height: 20px;
				outline: none;
				caret-color: #5e5a57;
				border-radius: 13px;
				margin: 0px 10px 0px 0px;
			}
			.nonstyle {
				background: #eaedf2;
				border: none;
				padding: 4px 10px;
				font-size: 14px;
				color: #5e5a57;
				height: 20px;
				outline: none;
				caret-color: #5e5a57;
				border-radius: 13px;
				box-shadow: inset 3px 4px 7px #cccfd4, inset -3px -4px 3px white;
				margin: 0px 10px 0px 0px;
				&:focus{
				box-shadow: inset 3px 4px 7px #acd3bd, inset -3px -4px 3px white;
				}
			}
		}
		.errorText {
			display: inline-block;
			height: 20px;
			max-width: 200px;
			color: red;
			border-radius: 5px;
		}
	}
}
.homework3_button {
	background: #eaedf2;
	padding: 5px 10px;
	font-size: 14px;
	color: #5e5a57;
	border: none;
	caret-color: #5e5a57;
	border-radius: 13px;
	box-shadow: inset 3px 4px 7px white, inset -3px -4px 3px #cccfd4;
	margin: 0px 0px 3px 0px;
	cursor: pointer;
	&:active{
	box-shadow: inset 3px 4px 7px #cccfd4, inset -3px -4px 3px white;
	}
	&:focus-visible{
	outline: #5e5a57 1px solid;
	}
}
